import React, { FC, HTMLAttributes, useEffect, useMemo, useState, useCallback } from 'react';
import { Link } from 'gatsby';
import cls from 'classnames';

import SEO from 'components/SEO';
import css from './index.module.scss';
import ExternalLink from 'components/ExternalLink';
import AnimatedHi from 'components/AnimatedHi';

const Effect: FC<HTMLAttributes<HTMLDivElement>> = ({ children, ...props }) => (
  <div {...props} className={cls(css.effect, props.className)}>
    {children}
  </div>
);

type EffectComponentProps = { triggered: boolean };

const EffectComponents: FC<EffectComponentProps>[] = [
  ({ triggered }) => (
    <Effect className={cls(css['effect-3'], css.small, triggered && css['triggered-effect-3'])}>
      <AnimatedHi /> {console.log('1')}
    </Effect>
  ),
  ({ triggered }) => (
    <Effect className={cls(css['effect-1'], triggered && css['triggered-effect-1'])}>Hi! {console.log('2')}</Effect>
  ),
  ({ triggered }) => (
    <Effect className={cls(css['effect-2'], css.small, triggered && css['triggered-effect-2'])}>
      <div>Hi! {console.log('3')}</div>
    </Effect>
  ),
];

const random = () => Math.floor(Math.random() * EffectComponents.length);

/**
 * TODO: Fix the Hi component sorta flickering.
 */
const Home: FC<{}> = () => {
  const [expanded, setExpanded] = useState(false);
  const [effectIndex, setEffectIndex] = useState(0);

  useEffect(() => {
    const timer = setTimeout(() => setExpanded(true), 500);
    return () => clearTimeout(timer);
  }, []);

  const updateEffect = useCallback(() => setEffectIndex(random()), []);

  const EffectComponent = useMemo(() => {
    console.log(effectIndex);
    return EffectComponents[effectIndex];
  }, [effectIndex]);

  return (
    <div className={css.container}>
      <SEO title='Welcome' keywords={[`jayant bhawal`, `frontend`, `javascript`, `react`, `typescript`, `gatsby`]} />
      <div className={css['effect-wrapper']} onClick={updateEffect}>
        <EffectComponent triggered={expanded} />
      </div>
      <h1 className={css.h1}>I'm Jayant Bhawal</h1>
      <nav className={css.navigation}>
        <Link to={'/about'}>About</Link>
        <Link to={'/blog'}>Blog</Link>
        <ExternalLink to={'https://jayant.dev'} noIcon={true}>
          Experiments
        </ExternalLink>
      </nav>
    </div>
  );
};

export default Home;
